<template>
    <div>
        <v-row>
            <v-col>
                <v-data-table
                    :headers="headers"
                    :items="productItem"
                    item-key="id"
                    :loading="loading"
                    disable-sort
                    hide-default-footer
                    class="pl-2"
                >
                    <!-- eslint-disable  -->
                    <template v-slot:item.code="{item}">
                        <div class="d-flex">
                            <span class="text-black">#{{ item.code }}</span>
                        </div>
                    </template>
                    <template v-slot:item.type="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.type }}</span>
                        </div>
                    </template>
                    <template v-slot:item.name="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.name }}</span>
                        </div>
                    </template>


                    <template v-slot:item.actions="{ item }">
                        <div class="d-flex justify-end">
                            <edit v-if="$can('update', 'codebase_tayyor')" :item="item" @updateTable="updateTable($event)"></edit>
                            <!-- <removeModal v-if="$can('delete', 'codebase_tayyor')" :item="item" @updateTable="updateTable($event)"></removeModal> -->
                        </div>
                    </template>

                    <template v-slot:no-data>
                        <div>
                            <no-data class="mt-6"></no-data>
                            <br>
                            <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                        </div>
                    </template>

                </v-data-table>
            </v-col>
            <div style="border-right: 1px solid #ececec"></div>
            <v-col>
                <v-data-table
                    :headers="headers"
                    :items="productItemSecond"
                    item-key="id"
                    :loading="loading"
                    disable-sort
                    hide-default-footer
                >
                    <!-- eslint-disable  -->
                    <template v-slot:item.code="{item}">
                        <div class="d-flex">
                            <span class="text-black">#{{ item.code }}</span>
                        </div>
                    </template>
                    <template v-slot:item.type="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.type }}</span>
                        </div>
                    </template>
                    <template v-slot:item.name="{item}">
                        <div class="d-flex justify-center">
                            <span class="text-black">{{ item.name }}</span>
                        </div>
                    </template>


                    <template v-slot:item.actions="{ item }">
                        <div class="d-flex justify-end">
                            <edit v-if="$can('update', 'codebase_tayyor')" :item="item" @updateTable="updateTable($event)"></edit>
                            <!-- <removeModal v-if="$can('delete', 'codebase_tayyor')" :item="item" @updateTable="updateTable($event)"></removeModal> -->
                        </div>
                    </template>

                    <template v-slot:no-data>
                        <div>
                            <no-data class="mt-6"></no-data>
                            <br>
                            <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                        </div>
                    </template>

                </v-data-table>
            </v-col>
        </v-row>
        <v-row>
            <v-col>
                <v-pagination
                    v-model="page"
                    :length="pageCount"
                    total-visible="7"
                    class="my-2"
                ></v-pagination>
            </v-col>
        </v-row>
    </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import NoData from "../base/NoData";
// import removeModal from '@/components/modals/Codebase/delete'
import edit from   '@/components/modals/Codebase/edit'
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    mixins: [loaderMixin],
    components: {
        NoData,
        edit,
        // removeModal
    },
    data() {
        return {
            page: 1,
            pageCount: 0,
            productItem: [],
            productItemSecond: [],
            type: '5',
            headers: [
                {text: 'Kodi', value: 'code', align: 'start', class: 'table-header'},
                {text: 'Turi', value: 'type', align: 'center', class: 'table-header'},
                {text: 'Nomi', value: 'name', align: 'center', class: 'table-header'},
                {text: 'Harakatlar', value: 'actions', align: 'end', class: 'table-header'},
            ],
        }
    },
    watch: {
        page: function (newVal, oldVal) {
            if (newVal) {
                this.refresh()
            }
        },
        '$store.state.main.codebase_ready_products': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_CODEBASE_READY_PRODUCTS', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    async mounted() {
        await this.refresh();
        if(!this.$can('update', 'codebase_tayyor')){
            this.headers.pop()
        }
    },
    computed: {
        ...mapState(
            {
                productCodesFields: state => state.code.productCodesFields,
                searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
            }
        ),
    },
    methods: {
        ...mapActions(
            {fetchProductCodes: 'code/fetchProductCodes'}
        ),
        async refresh() {
            this.loading = true
            await this.fetchProductCodes({page: this.page, type: this.type, search: this.searchValue, date: this.dateFilterValue});

            this.page = this.productCodesFields.current_page;
            this.pageCount = Math.ceil(this.productCodesFields.total / 20);
            this.productItemSecond = this.productCodesFields.data.slice(10, this.productCodesFields.data.length);
            this.productItemSecond.forEach((elem) => {
                elem.type = elem.product_category.name
            })
            this.productItem = this.productCodesFields.data;
            this.productItem.forEach((elem) => {
                elem.type = elem.product_category.name
            })

            this.loading = false
        },
        editItem() {
            console.log('edited')
        },
        deleteItem() {
            console.log('deleted!')
        },
        async updateTable(e){
            if(e){
                await this.refresh();
            }
        },
    }
}
</script>

<style scoped>

</style>
