<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="addModal"
        >
            <template v-slot:activator="{ on, attrs }">
                <v-btn dark class="mr-2" elevation="0" v-bind="attrs"
                    v-on="on" style="background: #2886FF!important; text-transform: capitalize;">
                    <img src="@/assets/icons/add.svg"
                        alt="add" style="width: 23px;">
                    Qo‘shish
                </v-btn>
            </template>

            <v-card class="aksessuar_add">
                <div class="aksessuar_add__title">
                    <span class="text-black">Yangi mahsulot</span>
                    <v-btn icon @click="closeModal">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                <ValidationObserver ref="formAdd" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">

                        <div class="mt-4">
                                <span class="text-black ml-1">Sana</span>
                                
                                    <v-menu
                                        v-model="dateMenu"
                                        :close-on-content-click="false"
                                        :nudge-right="40"
                                        transition="scale-transition"
                                        offset-y
                                        min-width="auto"
                                    >
                                        <template v-slot:activator="{ on, attrs }">
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <v-text-field
                                                    class="mt-2"
                                                    placeholder="Tanlang"
                                                    v-model="dateFormatted"
                                                    readonly
                                                    outlined
                                                    v-bind="attrs"
                                                    v-on="on"
                                                    @blur="date = parseDate(dateFormatted)"
                                                    :error-messages="errors"
                                                ></v-text-field>
                                            </ValidationProvider>
                                        </template>
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                        <v-date-picker
                                            color="blue lighten-1"
                                            v-model="date"
                                            @input="dateMenu = false"
                                            locale="ru-ru"
                                            first-day-of-week="1"
                                            :error-messages="errors"
                                        ></v-date-picker>
                                        </ValidationProvider>
                                    </v-menu>
                            </div>
                
                        <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'category']}" v-slot="{ errors }">
                            <span class="text-black ml-1">Kategoriyasi</span>
                            
                            <v-select class="mt-2" :items="selectItems" item-text="name" item-value="id"
                                v-model="parentCategoryId" label="Misol: Yoqa" outlined :error-messages="errors">
                            </v-select>
                        </ValidationProvider>


                        <ValidationProvider v-if="parentCategoryId != 1"
                         :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'type']}"
                          v-slot="{ errors }">
                            <span class="text-black ml-1">Turi</span>
                            
                            <v-select :items="selectItemsSecond"
                                v-model="categoryId" class="mt-2" item-text="name"
                                item-value="id" label="Misol: Manjet" outlined :error-messages="errors">
                            </v-select>
                        </ValidationProvider>


                        <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'name']}" v-slot="{ errors }">
                            <span class="text-black ml-1">Nomi</span>
                          
                            <v-text-field outlined v-model="thread.name" label="Misol: Vilvit"
                                class="mt-2" :error-messages="errors">
                            </v-text-field>
                        </ValidationProvider>

                        <ValidationProvider :rules="{required: true, error: [errorStatus, errorMessage, errorPlace, 'code']}" v-slot="{ errors }">
                            <span class="text-black ml-1">Kodi</span>
                            
                            <v-text-field outlined v-model="thread.code"
                                :counter="8" label="Misol: 000000"
                                class="mt-2" :error-messages="errors">
                            </v-text-field>
                        </ValidationProvider>

                        <!-- <ValidationProvider :rules="{error: [errorStatus, errorMessage, errorPlace, 'tavsif']}" v-slot="{ errors }">
                            <span class="text-black ml-1">Tavsif</span>
                            
                            <v-textarea label="Misol: Qora rangdagi manjetli yoqa." v-model="thread.description" auto-grow outlined rows="3"
                                class="mt-4" row-height="15" :error-messages="errors">
                            </v-textarea>
                        </ValidationProvider> -->


                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeModal"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Qo‘shish
                            </v-btn>
                        </v-card-actions>
                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import {mapActions, mapMutations, mapState} from "vuex";
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    mixins: [dateMixin, loaderMixin],
    data() {
        return {
            // ============= SECONDARY_DATA =============
            addModal: false,
            parentCategoryId: '',
            categoryId: '',
            thread: {
                name: '',
                code: '',
                // description: '',
            },
            selectItemsSecond: [],
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted() {
        await this.refresh();
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        parentCategoryId(newVal, oldVal) {
            if(newVal == 1){
                this.categoryId = 1
            }else if (newVal == 2) {
                this.selectItemsSecond = this.productCategories.filter((elem) => elem.parent_id == 2)
            } else if (newVal == 3) {
                this.selectItemsSecond = this.productCategories.filter((elem) => elem.parent_id == 3)
            } else if (newVal == 4) {
                this.selectItemsSecond = this.productCategories.filter((elem) => elem.parent_id == 4)
            } else if (newVal == 5) {
                this.selectItemsSecond = this.productCategories.filter((elem) => elem.parent_id == 5)
            }
        },
        'thread': {
            handler(newVal){
                if(value){
                    this.clearErrors()
                }   
            },deep: true
        },
        // ============= OTHER_WATCHERS END =============
    },
    computed: {
        ...mapState(
            {
                productCategories: state => state.categories.productCategories,
            }
        ),
        selectItems() {
            return this.productCategories.slice(0, 5);
        },
    },
    methods: {
        ...mapMutations(
            {
            }
        ),
        ...mapActions(
            {fetchCategories: 'categories/fetchCategories', saveProductCode: 'code/saveProductCode'},
        ),

        // ============= REFRESH START =============
        async refresh() {
            this.loading = true
            await this.fetchCategories();
            this.loading = false
        },
        // ============= REFRESH START =============




        



        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.date = ''

            this.parentCategoryId = ''
            this.thread.name = ''
            this.thread.code = ''
            // this.thread.description = ''
            this.categoryId = ''
            this.selectItemsSecond = []
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeModal(){
            this.clearInputs();
            this.addModal = false
            this.$refs.formAdd.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============















        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formAdd.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============


                    // ============= ADDING_NEW_PRODUCT_CODE START =============
                    let a = await this.saveProductCode({
                        code: parseFloat(this.thread.code.toString().replace(/,/g, '')),
                        product_category_id: parseInt(this.categoryId.toString().replace(/,/g, '')),
                        name: this.thread.name,
                        // description: this.thread.description
                    })

                    if (a.response && a.response.status == 422) {
                        if(a.response.data.errors.code){
                            this.errorMessage = a.response.data.errors.code[0];
                            this.errorPlace = 'code'
                        }else if(a.response.data.errors.name){
                            this.errorMessage = a.response.data.errors.name[0];
                            this.errorPlace = 'name'
                        }
                        this.errorStatus = true;
                        this.$toast.error(this.errorMessage);
                    }else if(a.response && a.response.status == 409){
                        if(a.response.data.message){
                            this.errorMessage = a.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        if(this.parentCategoryId == 1){
                            this.$store.commit('main/SET_UPDATE_CODEBASE_THREAD', true)
                        }else if(this.parentCategoryId == 2){
                            this.$store.commit('main/SET_UPDATE_CODEBASE_MATERIAL', true)
                        }else if(this.parentCategoryId == 3){
                            this.$store.commit('main/SET_UPDATE_CODEBASE_COLLAR', true)
                        }else if(this.parentCategoryId == 4){
                            this.$store.commit('main/SET_UPDATE_CODEBASE_AKSESSUAR', true)
                        }else if(this.parentCategoryId == 5){
                            this.$store.commit('main/SET_UPDATE_CODEBASE_READY_PRODUCTS', true)
                        }

                        this.$toast.success('Muvaffaqiyatli qo`shildi!');
                        this.closeModal();
                    }
                    // ============= ADDING_NEW_PRODUCT_CODE END =============
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        },
        // ============= ON_SUBMIT END =============
        
        
    }
}
</script>

