<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="editModal"
        >
            <template v-slot:activator="{ }">
                <div @click="openEdit()" class="cursor-pointer mt-1 mr-2">
                    <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                </div>
            </template>

            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Mahsulot tahriri</span>
                    <v-btn icon @click="closeModal()">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>
                
                <ValidationObserver ref="formEditCodebase" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">
                       
                        <div class="mt-5">
                            <span class="text-black ml-1">Nomni kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'name']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.name" class="mt-2"
                                    placeholder="Misol: Vilvit" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Kodni kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'code']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.code" class="mt-2"
                                    placeholder="Misol: 666777" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                            
                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeModal()"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tahrirlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import {mapState, mapActions} from "vuex";
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    props: ['item'],
    mixins: [dateMixin, loaderMixin],
    data() {
        return {
            // ============= SECONDARY_DATA =============
            editModal: false,
            product: {
                name: '',
                code: '',
            },
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    computed: {
        ...mapState(
        {
            productCategories: state => state.categories.productCategories,
        }),
        filteredItems() {
            return this.productCategories.slice(0, 5);
        }
    },
    methods: {
        ...mapActions(
        {
            getProductId: 'code/getProductId',
            editProduct: 'code/editProduct',
        }),
        async openEdit() {
            this.product = await this.getProductId(this.item)
            this.editModal = true
        },




        
        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.product.name = ''
            this.product.code = ''
            // this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeModal(){
            this.clearInputs();
            this.editModal = false
            this.$refs.formEditCodebase.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============






        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formEditCodebase.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============



                    // ============= ADDING_NEW_IP START =============
                    let product = await this.editProduct(this.product)

                    if(product.response && product.response.status == 422){
                        if(product.response.data.errors.code){
                            this.errorMessage = product.response.data.errors.code[0];
                            this.errorPlace = 'code'
                        }else if(product.response.data.errors.name){
                            this.errorMessage = product.response.data.errors.name[0];
                            this.errorPlace = 'name'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(product.response && product.response.status == 409){
                        if(product.response.data.message){
                            this.errorMessage = product.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        this.$emit('updateTable', true)
                        let toastTitle = 'Muvaffaqiyatli tahrirlandi!'
                        this.$toast.success(toastTitle);
                        this.closeModal();
                    }
                    // ============= ADDING_NEW_IP END =============


                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT END =============
    },
 
}
</script>

<style scoped>

</style>
