<template>
    <v-dialog
        max-width="400"
        persistent
        v-model="deleteModal"
    >
        <template v-slot:activator="{  }">
            <div @click="deleteItem" class="mt-1 cursor-pointer">
                <img src="@/assets/icons/trush-square.svg" alt="edit-2">
            </div>
        </template>
        <v-card class="aksessuar_add">

            <div class="aksessuar_add__title">
                <span class="text-black">Mahsulot o‘chirilishi</span>
                <v-btn icon @click="closeModal">
                    <img src="@/assets/images/BackButton.svg" alt="BackButton">
                </v-btn>
            </div>

            <div class="pb-5 pt-4 mb-3 text-muted border-bottom">
                Bu mahsulotni o‘chirmoqchimisiz?
            </div>

            <v-card-actions class="d-flex justify-space-between">
                <v-btn
                    text
                    class="backBtn"
                    elevation="0"
                    @click="closeModal"
                >Yo‘q
                </v-btn>
                <v-btn
                    class="primaryBtn"
                    elevation="0"
                    @click="removeProduct"
                    :loading="loading"
                >Ha
                </v-btn>
            </v-card-actions>

        </v-card>
    </v-dialog>

</template>

<script>
import {mapState, mapActions} from "vuex";
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    mixins: [dateMixin, loaderMixin],
    props: ['item'],
    data() {
        return {
            deleteModal: false,
        }
    },
    computed: {
        ...mapState(
        {
            product: state => state.code.product,
        }),
    },
    methods: {
        ...mapActions(
        {
            getProductId: 'code/getProductId',
            removeProduct: 'code/removeProduct',
        }),
        closeModal(){
            this.deleteModal = false
        },
        deleteItem() {
            this.deleteModal = !this.deleteModal
            this.getProductId(this.item)
        },
        async removeProduct() {
            this.loader = 'loading'
            await this.$store.dispatch('code/removeProduct', this.item)
            this.$emit('updateTable', true)
            this.closeModal()
            let toastTitle = 'Muvaffaqiyatli ochirildi!'
            this.$toast.success(toastTitle);
        }
    },
   
}
</script>

