<template>
    <div class="codebase" id="codebase">

        <div class="tabs">
            <v-tabs
                v-model="tab"
                color="black"
            >
                <v-tabs-slider color="teal lighten-3"></v-tabs-slider>

                <v-tab href="#tab-1" @click="routeNAME = 'codebase-ip'" v-if="$can('read', 'codebase_ip')"> 
                    Ip
                </v-tab>

                <v-tab href="#tab-2" @click="routeNAME = 'codebase-mato'" v-if="$can('read', 'codebase_mato')">
                    Mato
                </v-tab>

                <v-tab href="#tab-3" @click="routeNAME = 'codebase-yoqa'" v-if="$can('read', 'codebase_yoqa')">
                    Yoqa
                </v-tab>

                <v-tab href="#tab-4" @click="routeNAME = 'codebase-aksessuar'" v-if="$can('read', 'codebase_aksessuar')">
                    Aksessuar
                </v-tab>

                <v-tab href="#tab-5" @click="routeNAME = 'codebase-tayyor-mahsulotlar'" v-if="$can('read', 'codebase_tayyor')">
                    Tayyor mahsulotlar
                </v-tab>
            </v-tabs>
            <v-col cols="auto" v-if="$can('read', 'codebase_qoshish')">
                <div class="d-flex align-center">
                    <ModalCodeBaseAdd></ModalCodeBaseAdd>
                </div>
            </v-col>
        </div>

        <v-divider></v-divider>

        <v-tabs-items v-model="tab">

            <v-tab-item :value="'tab-1'" v-if="$can('read', 'codebase_ip')">

                <code-item-thread></code-item-thread>

            </v-tab-item>

            <v-tab-item :value="'tab-2'" v-if="$can('read', 'codebase_mato')">

                <code-item-material></code-item-material>

            </v-tab-item>

            <v-tab-item :value="'tab-3'" v-if="$can('read', 'codebase_yoqa')">

                <code-item-collar></code-item-collar>

            </v-tab-item>

            <v-tab-item :value="'tab-4'" v-if="$can('read', 'codebase_aksessuar')">

                <code-item-accessory></code-item-accessory>

            </v-tab-item>

            <v-tab-item :value="'tab-5'" v-if="$can('read', 'codebase_tayyor')">

                <code-item-product></code-item-product>

            </v-tab-item>

        </v-tabs-items>
    </div>
</template>

<script>
/* eslint-disable */
import ModalCodeBaseAdd from '@/components/modals/Codebase/add.vue'
import CodeItemThread from "@/components/code_products/CodeItemThread";
import CodeItemMaterial from "@/components/code_products/CodeItemMaterial";
import CodeItemCollar from "@/components/code_products/CodeItemCollar";
import CodeItemAccessory from "@/components/code_products/CodeItemAccessory";
import CodeItemProduct from "@/components/code_products/CodeItemProduct";

export default {
    components: {
        CodeItemProduct,
        CodeItemAccessory,
        CodeItemCollar,
        CodeItemMaterial,
        CodeItemThread,
        ModalCodeBaseAdd,
    },
    data() {
        return {
            addModal: false,
            editModal: false,
            deleteModal: false,
            tab: '',
        }
    },
    computed: {
        routeNAME: {
            get: function() {
                return this.$store.state.main.routeNAME
            },
            set: function(newValue) {
                return this.$store.state.main.routeNAME = newValue
            }
        },
    },
    methods: {
        editItem() {
            this.editModal = true
        },
        deleteItem() {
            this.deleteModal = true
        },
        closeAdd(e) {
            this.addModal = false
        },
        closeEdit(e) {
            this.editModal = false
        },
        closeDelete(e) {
            this.deleteModal = false
        }
    },
    mounted() {
        
    }
}
</script>

